* {font-family: Arial, Helvetica, sans-serif;}
body {padding: 60px}
input {width: 100%; font-size: 1.3rem; padding: 5px 8px; color: #666}
.card-grid {display: flex; flex-direction: row; flex-wrap: wrap;}
.card { align-content: center; border: 1px solid grey; border-radius: 6px; overflow: hidden; margin:0 10px 10px 0;}
.card p {padding: 5px; text-align: center; font-size: 14px}
.card img {max-height: 170px;}